export function WeeklyScheduleView() {
  const { driver } = useAuth()
  const [currentWeekStart, setCurrentWeekStart] = useState(
    new Date(2024, 0, 8), // Monday, January 8, 2024
  )

  const weekDates = getWeekDates(currentWeekStart)
  const schedule = MOCK_SCHEDULE.default

  const handlePreviousWeek = () => {
    setCurrentWeekStart(new Date(currentWeekStart.getTime() - 7 * 24 * 60 * 60 * 1000))
  }

  const handleNextWeek = () => {
    setCurrentWeekStart(new Date(currentWeekStart.getTime() + 7 * 24 * 60 * 60 * 1000))
  }

  return (
    <div className="p-10">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="flex items-center justify-between mb-10">
          <div>
            <h1 className="text-4xl font-bold text-foreground mb-3">Planning Semaine</h1>
            <p className="text-lg text-muted-foreground">{driver.city}</p>
          </div>

          {/* Week Navigation */}
          <div className="flex items-center gap-4">
            <button onClick={handlePreviousWeek} className="p-2 hover:bg-accent rounded-lg transition-colors">
              <ChevronLeft className="w-6 h-6 text-foreground" />
            </button>
            <span className="text-base font-medium text-foreground min-w-52 text-center">
              {formatDate(currentWeekStart)} -{" "}
              {formatDate(new Date(currentWeekStart.getTime() + 6 * 24 * 60 * 60 * 1000))}
            </span>
            <button onClick={handleNextWeek} className="p-2 hover:bg-accent rounded-lg transition-colors">
              <ChevronRight className="w-6 h-6 text-foreground" />
            </button>
          </div>
        </div>

        {/* Weekly Grid */}
        <div className="grid grid-cols-7 gap-4 mb-10">
          {schedule.map((daySchedule, idx) => (
            <div
              key={idx}
              className={`rounded-lg border ${
                daySchedule.isRestDay ? "border-border bg-muted" : "border-border bg-card"
              } overflow-hidden`}
            >
              {/* Day Header */}
              <div
                className={`px-5 py-4 border-b border-border ${daySchedule.isRestDay ? "bg-muted/50" : "bg-accent"}`}
              >
                <p className="text-base font-semibold text-foreground">{daySchedule.day}</p>
                <p className="text-sm text-muted-foreground">{daySchedule.date}</p>
              </div>

              {/* Day Content */}
              <div className="p-5">
                {daySchedule.isRestDay ? (
                  <div className="text-center py-8">
                    <p className="text-lg font-medium text-muted-foreground">REPOS</p>
                  </div>
                ) : (
                  <div className="space-y-4">
                    {daySchedule.slots.map((slot, slotIdx) => (
                      <div
                        key={slotIdx}
                        className={`p-3 rounded text-sm border ${
                          slot.type === "break"
                            ? "bg-amber-50 border-amber-200 text-amber-700"
                            : "bg-emerald-50 border-emerald-200 text-emerald-700"
                        }`}
                      >
                        <p className="font-semibold">{slot.time}</p>
                        <p className="text-sm opacity-75 mt-1">{slot.bus}</p>
                        <p className="text-sm opacity-75">{slot.route}</p>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            </div>
          ))}
        </div>

        {/* Legend */}
        <div className="flex gap-6 text-sm">
          <div className="flex items-center gap-2">
            <div className="w-4 h-4 rounded bg-emerald-50 border border-emerald-200" />
            <span className="text-foreground">Service</span>
          </div>
          <div className="flex items-center gap-2">
            <div className="w-4 h-4 rounded bg-amber-50 border border-amber-200" />
            <span className="text-foreground">Pause</span>
          </div>
          <div className="flex items-center gap-2">
            <div className="w-4 h-4 rounded bg-muted border border-border" />
            <span className="text-foreground">Repos</span>
          </div>
        </div>
      </div>
    </div>
  )
}